}

.overview-label {
  font-size: 24rpx;
  color: #666;
}

/* ÊàøÈó¥Âá∫ÁßüÊÉÖÂÜµ */
.occupancy-section {
  background: white;
  margin: 0 40rpx 30rpx;
  border-radius: 20rpx;
  padding: 40rpx 30rpx;
  box-shadow: 0 8rpx 25rpx rgba(0,0,0,0.1);
}

.section-title {
  font-size: 32rpx;
  font-weight: bold;
  color: #333;
  margin-bottom: 30rpx;
}

.occupancy-main {
  display: flex;
  align-items: center;
  margin-bottom: 30rpx;
}

.occupancy-rate {
  margin-right: 40rpx;
}

.rate-circle {
  width: 140rpx;
  height: 140rpx;
  border-radius: 50%;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.rate-text {
  font-size: 32rpx;
  font-weight: bold;
}

.rate-label {
  font-size: 20rpx;
  margin-top: 4rpx;
}

.occupancy-details {
  flex: 1;
  display: flex;
  justify-content: space-around;
}

.detail-item {
  text-align: center;
}

.detail-number {
  font-size: 36rpx;
  font-weight: bold;
  color: #333;
  margin-bottom: 8rpx;
}

.detail-item.rented .detail-number {
  color: #f44336;
}

.detail-item.available .detail-number {
  color: #4caf50;
}

.detail-label {
  font-size: 24rpx;
  color: #666;
}

.rental-alerts {
  border-top: 1rpx solid #f0f0f0;
  padding-top: 20rpx;
}

.alert-item {
  display: flex;
  align-items: center;
  padding: 16rpx;
  margin-bottom: 12rpx;
  border-radius: 12rpx;
}

.alert-item.warning {
  background: #fff3cd;
  border: 1rpx solid #ffeaa7;
}

.alert-item.danger {
  background: #f8d7da;
  border: 1rpx solid #f5c6cb;
}

.alert-icon {
  margin-right: 12rpx;
  font-size: 28rpx;
}

.alert-text {
  font-size: 26rpx;
  color: #333;
}

/* Êî∂ÂÖ•Ë∂ãÂäøÂõæ */
.trend-section {
  background: white;
  margin: 0 40rpx 30rpx;
  border-radius: 20rpx;
  padding: 40rpx 30rpx;
  box-shadow: 0 8rpx 25rpx rgba(0,0,0,0.1);
}

.trend-chart {
  display: flex;
  justify-content: space-between;
  align-items: end;
  height: 200rpx;
  margin-bottom: 20rpx;
}

.trend-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 1;
}

.trend-bar {
  position: relative;
  width: 40rpx;
  height: 160rpx;
  background: #f0f0f0;
  border-radius: 20rpx;
  margin-bottom: 12rpx;
  overflow: hidden;
}

.trend-bar-fill {
  position: absolute;
  width: 100%;
  border-radius: 20rpx;
  transition: all 0.3s;
}

.trend-bar-fill.rent {
  background: linear-gradient(to top, #667eea 0%, #764ba2 100%);
}

.trend-bar-fill.utility {
  background: linear-gradient(to top, #f093fb 0%, #f5576c 100%);
}

.trend-label {
  font-size: 22rpx;
  color: #666;
  margin-bottom: 4rpx;
}

.trend-amount {
  font-size: 20rpx;
  color: #333;
  font-weight: bold;
}

.trend-legend {
  display: flex;
  justify-content: center;
  gap: 40rpx;
}

.legend-item {
  display: flex;
  align-items: center;
  font-size: 24rpx;
  color: #666;
}

.legend-color {
  width: 20rpx;
  height: 20rpx;
  border-radius: 4rpx;
  margin-right: 8rpx;
}

.legend-color.rent {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
}

.legend-color.utility {
  background: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);
}

/* Á≥ªÁªüÁÆ°ÁêÜÂÖ•Âè£ */
.system-management {
  padding: 40rpx;
  text-align: center;
}

.system-btn {
  background: linear-gradient(135deg, #ff6b6b 0%, #ee5a24 100%);
  color: white;
  border: none;
  border-radius: 16rpx;
  padding: 24rpx 48rpx;
  font-size: 28rpx;
  font-weight: bold;
  box-shadow: 0 8rpx 25rpx rgba(255, 107, 107, 0.3);
  transition: all 0.3s;
}

.system-btn:active {
  transform: scale(0.95);
  box-shadow: 0 4rpx 12rpx rgba(255, 107, 107, 0.4);
}

</style>
